import React, { useState } from 'react'
import axios from 'axios';
import LoginForm from './LoginForm';

function RegisterForm() {
    const [userName, setUserName] = useState('');
    const [pass, setPass] = useState('');
    const [passConfirm, setPassConfirm] = useState('');
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [avatar, setAvatar] = useState([]);
    const [error, setError] = useState('');

    const [isLogin, setIsLogin] = useState(false);
    const handleRegister = () => {
        setIsLogin(true)
    }

    const hanldeAvatar = (e) => {
        setAvatar([...avatar, e.target.files[0]])
        console.log(avatar);

    }

    const handleSubmit = async (e) => {
        e.preventDefault();


         console.log(avatar);

        if (pass === passConfirm) {



            console.log(avatar);
            try {
                await axios.post(
                    'https://api.chatengine.io/projects/people/',
                    { 'username': userName, 'secret': pass, 'first_name': firstName, 'last_name': lastName, 'avatar': avatar },
                    { headers: { "Private-Key": 'a1a26c4c-87e8-4fad-956f-65067beba9f8' } }
                )

                localStorage.setItem('username', userName);
                localStorage.setItem('password', pass);

                window.location.reload();
            } catch (error) {
                setError('Oops,credenciales incorrectas游땩')
            }
        } else {
            setError('Las contrase침as no coinciden')

        }




    }
    return (
        <div className="wrapper">

            {!isLogin ?
                <dir className="form">

                    <h1 className="title">
                        Registro
                </h1>
                    <form onSubmit={handleSubmit}>
                        <input type="text" value={userName}
                            onChange={(e) => setUserName(e.target.value)}
                            className="input-singUp "
                            placeholder="Usuario"
                            required

                        />

                        <input type="password" value={pass}
                            onChange={(e) => setPass(e.target.value)}
                            className="input-singUp "
                            placeholder="Contrase침a"
                            required

                        />
                        <input type="password" value={passConfirm}
                            onChange={(e) => setPassConfirm(e.target.value)}
                            className="input-singUp "
                            placeholder="Confirmar contrase침a"
                            required

                        />

                        <input type="text" value={firstName}
                            onChange={(e) => setFirstName(e.target.value)}
                            className="input-singUp "
                            placeholder="Nombre"
                            required

                        />
                        <input type="text" value={lastName}
                            onChange={(e) => setLastName(e.target.value)}
                            className="input-singUp "
                            placeholder="Apellido"
                            required

                        />
                        <input
                            onChange={hanldeAvatar}
                            className="input-singUp"
                            type="file"
                            file={avatar}
                            multiple={false}
                            disabled

                        />
                        <div align="center">
                            <button type="submit" className="button">
                                <span>Registrarme</span>
                            </button>
                            <button className="button-SingUp" onClick={handleRegister}>Iniciar sesi칩n</button>

                        </div>
                        <h2 className="error">{error}</h2>
                    </form>

                </dir> : <LoginForm />

            }
        </div>

    )
}

export default RegisterForm;
