import { useState } from "react";

import axios from 'axios';
import RegisterForm from "./RegisterForm";
function LoginForm() {
    const [userName, setUserName] = useState('');
    const [pass, setPass] = useState('');
    const [error, setError] = useState('');
    const [isRegistering, setIsRegistering] = useState(false);
    // const [singUp, setSingUp] = useState(false);
    // const [singIn, setSingIn] = useState(false);

    const handleRegister=()=>{
        setIsRegistering(true)
    }

    const handleSubmit = async (e) => {
        e.preventDefault();

        const authObject = {
            'Project-ID': 'ee5e68c0-1c9c-4939-9901-21d3c33179a2',
            'User-Name': userName, 'User-Secret': pass
        }

        try {
            await axios.get('https://api.chatengine.io/chats', {
                headers: authObject
            })

            localStorage.setItem('username', userName);
            localStorage.setItem('password', pass);

            window.location.reload();
        } catch (error) {
            setError('Oops,credenciales incorrectas游땩')
        }

    }

    return (
        <div className="wrapper">

          
            {!isRegistering?
                 <dir className="form">
              
                    <h1 className="title">
                        Cheap Teams    <br /> Iniciar sesi칩n
                </h1>
                    <form onSubmit={handleSubmit}>
                        <input type="text" value={userName}
                            onChange={(e) => setUserName(e.target.value)}
                            className="input"
                            placeholder="Usuario"
                            required

                        />

                        <input type="password" value={pass}
                            onChange={(e) => setPass(e.target.value)}
                            className="input"
                            placeholder="Contrase침a"
                            required

                        />

                        <div align="center">
                            <button type="submit" className="button">
                                <span>Iniciar sesi칩n</span>
                            </button>
                        {/* <h3 className="title-down">拘勇 Formulario de registro abajo xd 拘勇</h3> */}
                        <button className="button-SingUp" onClick={handleRegister}>Registrarse</button>


                        </div>

                        <h2 className="error">{error}</h2>
                    </form>

                </dir> 
                             : <RegisterForm />

}
        </div>
    )
}

export default LoginForm;
